.container {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.timer {
    font-size: 250px;
    margin-top: -400px;
    color: #fff;
}
.area-btn {
    margin-top: 170px;
    display: flex;
    gap: 10px;
}
.-btn {
    width: 200px;
    background-color: transparent;
    border: 5px solid #fff;
    font-size: 30px;
    text-align: center;
    border-radius: 10px;
    text-transform: uppercase;
    cursor: pointer;
}